*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}
.nav ul{
  width: 100vw;
  height:50px;
  display: flex;
  flex-direction: row;
  background-color: rgb(255, 233, 233);
  justify-content: space-around;
  align-items: center;
}
body{
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
.App{
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.logo{
  width: 200px;
  height: 200px;
  padding: 50px;
  display: flex;
  margin: auto;
  background-color: rgb(255, 216, 216);
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  font-family: 'Luckiest Guy', cursive;
  letter-spacing: 2px;
  color:rgb(85, 85, 183);
  text-shadow: 0 0 5px rgb(255, 149, 149);
  border: 15px solid rgb(255, 164, 84);
  box-shadow: 0 0 5px rgb(59, 59, 59);
  font-size: 5px;
}
.logo div{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
}

